html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  box-sizing: border-box;
}

body, h2, ul, p {
  margin: 0;
}

.cards {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  display: flex;
}

.nav__list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-align: center;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 1.4;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-wrapper {
  color: #060b35;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 10px;
  line-height: 1.4;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 1.5;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding-block: 12px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14286;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
